// Глобальные стили
:root {
  // Шрифты сайта
  --main-font: "Oswald", sans-serif;
  --additional-font: "Arial", "Helvetica", sans-serif;

  // Кастомные своийства цветов сайт
  --primary-color: #000000; // Основной цвет текста
  --font-color-light: #ffffff; // Светлый цвет текста
  --bg-color-light: #ffffff; // Светлый цвет фона
  --font-color-dark: #444444; // Темный цвет текста
  --bright-color: #68b738; // Акцентный цвет
  --backgroun-main-color: #f2f2f2; // Цвет фона страницы

  // Отступы заголовков
  --content-width: 1220px;
  --magic-indent: calc(50% - var(--content-width) / 2 - 20px);
}
